import React, { Component } from "react";
import { Link } from "react-router-dom";
import { DataContext } from "../Context";
import "../css/Products.css";

export class Products extends Component {
  static contextType = DataContext;

  render() {
    const { products } = this.context;
    return (
      <div id="product">
        {products.map((product) => (
          <div className="card" key={product.isbn}>
            <Link to={`/product/${product.isbn}`}>
              <img src={product.cover} alt="" />
            </Link>
            <div className="content">
              <h3>
                <Link to={`/product/${product.isbn}`}>{product.judul}</Link>
              </h3>
              <span>IDR.{product.harga}</span>
              <p>{product.penulis}</p>
              <button onClick={()=> this.context.addCart(product.isbn)}>Add to cart</button>
            </div>
          </div>
        ))}
      </div>
    );
  }
}

export default Products;
