import React, { Component } from 'react'
import {DataContext} from '../Context'
import {Link} from 'react-router-dom'
import '../css/Details.css'
import '../css/Cart.css'

export class Cart extends Component {
    static contextType = DataContext;

    componentDidMount(){
        this.context.getTotal();
    }

    render() {
        const {cart,increase,reduction,removeProduct,total} = this.context;
        if(cart.length === 0){
            return <h2 style={{textAlign:"center"}}>Nothings Product</h2>
        }else{
        return (
            <>
                {
                    cart.map(item =>(
                        <div className="details cart" key={item.isbn}>
                            <img src={item.cover} alt=""/>
                            <div className="box">
                                <div className="row">
                                    <h2>{item.judul}</h2>
                                    <span>IDR.{item.harga * item.count}</span>
                                </div>
                                <p>{item.penulis}</p>
                                <p>{item.sinopsis}</p>
                                <div className="amount">
                                    <button className="count" onClick={()=> reduction(item.isbn)}> - </button>
                                    <span>{item.count}</span>
                                    <button className="count" onClick={()=> increase(item.isbn)}> + </button>
                                </div>
                            </div>
                            <div className="delete" onClick={() => removeProduct(item.isbn)}>X</div>
                        </div>
                    ))
                }
                <div className="total">
                    <Link to="/payment">Payment</Link>
                    <h3>Total: IDR.{total}</h3>
                </div>
            </>
            )
        }
    }
}

export default Cart
