import React, { Component } from "react";

export const DataContext = React.createContext();

export class DataProvider extends Component {
  state = {
    products: [
      {
        isbn: "1234",
        judul: "Bumi",
        penulis: "Tere Liye",
        sinopsis:
          "Novel Bumi karya Tere Liye ini menceritakan tentang seorang anak perempuan yang berumur 15 tahun bernama Raib. Sejak kecil ia sudah mempunyai sebuah rahasia, yaitu kekuatan untuk bisa menghilang.",
        harga: 90000,
        cover:
          "https://upload.wikimedia.org/wikipedia/id/thumb/4/49/Bumi_%28sampul%29.jpg/220px-Bumi_%28sampul%29.jpg",
        count: 1,
      },
      {
        isbn: "123456",
        judul: "Bulan",
        penulis: "Tere Liye",
        sinopsis:
          "Buku Novel Bulan karya Tere Liye ini merupakan buku kedua dari serial novel Bumi. Jika di novel Bumi menceritakan petualangan di klan Bulan, maka novel Bulan ini menceritakan petualangan di klan Matahari.",
        harga: 90000,
        cover:
          "https://ebooks.gramedia.com/ebook-covers/33725/big_covers/ID_GPU2016MTH08BULA_B.jpg",
        count: 1,
      },
      {
        isbn: "54321",
        judul: "Si Anak Badai",
        penulis: "Tere Liye",
        sinopsis:
          "Novel Si Anak Badai karya Tere Liye ini mengisahkan tentang anak laki laki bernama Zaenal atau kerap disapa Za yang berasal dari kampung Muara Manowa. Ia bersama teman temannya berusaha mempertahankan kampung halaman mereka yang hendak digusur karena akan dibuat pelabuhan.",
        harga: 83000,
        cover:
          "https://bukurepublika.id/wp-content/uploads/2019/07/Si-anak-badai_dpn_low-600x910.jpg",
        count: 1,
      },
      {
        isbn: "01198",
        judul: "Ayah",
        penulis: "Andrea Hirata",
        sinopsis:
          "Novel ini berkisah tentang perjuangan serta perasaan sayang seorang ayah kepada anaknya, tanpa mengenal ikatan darah sekalipun.",
        harga: 93000,
        cover:
          "https://upload.wikimedia.org/wikipedia/id/f/f3/Ayah_%28novel%29.jpeg",
        count: 1,
      },
      {
        isbn: "198228",
        judul: "Laskar Pelangi",
        penulis: "Andrea Hirata",
        sinopsis:
          "Laskar Pelangi adalah novel pertama karya Andrea Hirata yang diterbitkan oleh Bentang Pustaka pada tahun 2005. Novel ini bercerita tentang kehidupan 10 anak dari keluarga miskin yang bersekolah (SD dan SMP) di sebuah sekolah Muhammadiyah di Belitung yang penuh dengan keterbatasan.",
        harga: 89000,
        cover:
          "https://upload.wikimedia.org/wikipedia/en/6/68/Laskar_Pelangi_Poster.jpg",
        count: 1,
      },
      {
        isbn: "893423",
        judul: "Percy Jackson",
        penulis: "Rick Riordan",
        sinopsis:
          "Novel ini menceritakan tentang petualangan seorang pemuda bernama Percy Jackson, yang merupakan anak dewa Poseidon dan dituduh mencuri petir Zeus.",
        harga: 103000,
        cover:
          "https://images-na.ssl-images-amazon.com/images/I/81vpJ5Kr-iL.jpg",
        count: 1,
      },
      {
        isbn: "723494",
        judul: "Jika Kita Tak Jadi Apa Apa",
        penulis: "Alvi Syahrin",
        sinopsis:
          "Setelah sukses dengan buku motivasinya bertajuk Jika Kita Tak Pernah Jatuh Cinta, penulis Alvi Syahrin kini menerbitkan karya selanjutnya yang diberi judul Jika Kita Tak Pernah Jadi Apa-Apa.",
        harga: 69000,
        cover:
          "https://i.gr-assets.com/images/S/compressed.photo.goodreads.com/books/1574005472l/48844164._SX318_.jpg",
        count: 1,
      },
      {
        isbn: "119280",
        judul: "Geez&Ann",
        penulis: "Rintik Sedu",
        sinopsis:
          "Geez & Ann berkisah tentang perjalanan sepasang anak muda yang tengah jatuh cinta dan menemukan arti sebuah komitmen.",
        harga: 70000,
        cover:
          "https://id-test-11.slatic.net/p/b90ad8376cc7ec89c0475b49314ad98b.jpg",
        count: 1,
      },
      {
        isbn: "563329",
        judul: "The Happy Investor",
        penulis: "Jefferly Helianthusonfri",
        sinopsis:
          "Bagaimana menjadi investor saham yang bahagia dan mendapatkan keuntungan optimal",
        harga: 63000,
        cover:
          "http://togamas.com/css/images/items/potrait/togamas1_17000_The_Happy_Investor.jpg",
        count: 1,
      },
    ],
    cart: [],
    total: 0

  };

  addCart = (id) =>{
    const {products, cart} = this.state
    const check = cart.every(item =>{
      return item.isbn !== id
    })
    if(check){
      const data = products.filter(product =>{
        return product.isbn === id
      })
      this.setState({cart: [...cart, ...data]})
    }else{
      alert("The product has been added to cart.")
    }
  };

  reduction = (id) =>{
    const {cart} = this.state;
    cart.forEach(item =>{
      if(item.isbn === id){
        item.count === 1 ? item.count = 1 : item.count-=1;
      }
    })
    this.setState({cart: cart});
    this.getTotal();
  };

  increase = (id) =>{
    const {cart} = this.state;
    cart.forEach(item =>{
      if(item.isbn === id){
        item.count += 1;
      }
    })
    this.setState({cart: cart});
    this.getTotal();
  };

  removeProduct = (id) =>{
    if(window.confirm("Do you want to delete this product?")){
    const {cart} = this.state;
    cart.forEach((item, index) =>{
      if(item.isbn === id){
        cart.splice(index, 1)
      }
    })
    this.setState({cart: cart})
    this.getTotal();
  }
  };

  getTotal = () =>{
    const {cart} = this.state;
    const res = cart.reduce((prev, item) => {
      return prev + (item.harga * item.count);
    }, 0)
    this.setState({total: res})
  };

  componentDidUpdate(){
    localStorage.setItem('dataCart', JSON.stringify(this.state.cart))
    localStorage.setItem('dataTotal', JSON.stringify(this.state.total))
  };

  componentDidMount(){
    const dataCart = JSON.parse(localStorage.getItem('dataCart'));
    if(dataCart !== null){
        this.setState({cart: dataCart});
    }
    const dataTotal = JSON.parse(localStorage.getItem('dataTotal'));
    if(dataTotal !== null){
        this.setState({total: dataTotal});
    }
  };



  render() {
    const { products,cart, total } = this.state;
    const {addCart,reduction,increase,removeProduct,getTotal} = this;
    return (
      <DataContext.Provider value={{ products, addCart, cart, reduction, increase,removeProduct, total, getTotal }}>
        {this.props.children}
      </DataContext.Provider>
    );
  }
}
